.home-section {
	background: $paper;
	color: #333;
	padding: 1.5em 1.8em;
	.headline {
		margin-bottom: 1em;
		font-size: 1.9em;
		font-family: $headerFont;
	}
	p {
		font-size: 1.1em;
		text-align: left;
	}
}

.home-header {
	width: 100%;
	height: 100vh;
	align-items: center;
	color: #fff;
	background: $homeHeaderBg $homeHeaderImage;
	background-blend-mode: overlay;
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	background-attachment: fixed;
	overflow: hidden;
	.header-content {
		width: 94%;
		position: absolute;
		top: 45%;
		left: 50%;
		transform: translate(-50%, -50%);
		margin-top: -1em;
		text-align: center;
		h1,
		.home-meal {
			animation: appearWithOpacity 2s;
			animation-fill-mode: forwards;
			transition: opacity 300ms;
			text-shadow: 2px 2px 2px #000;
			font-family: $headerFont;
			opacity: 0;
		}
		h1 {
			z-index: 30;
			line-height: 1.2;
			padding-top: 2.8em;
			padding-bottom: 5px;
			font-size: 2.5em;
			animation-delay: .5s;
		}
		.home-meal {
			font-size: 1.2em;
			animation-delay: .7s;
			position: relative;
			a {
				display: inline-block;
				color: #fff;
				transition: padding .3s, transform .2s, background-color .5s;
				transform: scale(1);
				border-radius: 20px;
				&:hover {
					transform: scale(1.2);
					transition: padding .3s, transform .2s, background-color .5s;
					background: $mainDark;
					padding: 0 15px;
					box-shadow: 2px 2px 15px rgba(0, 0, 0, .6);
				}
			}
		}
	}
	.header-bg-content {
		padding: 1em;
	}
	.home-button {
		display: flex;
		margin: .6em auto;
		width: 5em;
		height: 5em;
		text-align: center;
		border-radius: 50%;
		background: $main;
		color: #fff;
		animation: appearWithRotate 1s;
		animation-fill-mode: forwards;
		transition: opacity 300ms;
		animation-delay: 1s;
		opacity: 0;
		position: relative;
		border: none;
		i {
			margin: auto;
			font-size: 40px;
		}
		&:hover {
			background: $mainHover;
			transition: transform 400ms, background-color 400ms;
		}
	}
	.home-search {
		width: 100%;
		max-width: 30em;
		position: absolute;
		top: 1em;
		left: 50%;
		transform-origin: top;
		transition: transform 400ms;
		transform: translateX(-50%) perspective(1000px) rotateX(-90deg);
	}
	.home-search.active {
		transform: translateX(-50%) perspective(1000px) rotateX(0deg);
	}
	.header-search input {
		width: 88%;
		position: absolute;
		top: 4.6em;
		left: 0;
		height: 2em;
		padding: 0 1em;
		border-radius: 10px;
		border: 2px solid #1e5799;
		background: rgba(0, 0, 0, .4);
		color: #ffffff;
		transition: border 300ms, opacity 400ms;
		outline: 0;
		&:focus {
			border: 2px solid #861b50;
			transition: border 300ms;
		}
	}
}

.edit-form {
	position: absolute;
	width: 80%;
	max-width: 35em;
	top: 100%;
	left: 50%;
	z-index: 100;
	background: #eee;
	border-radius: 4px;
	box-shadow: 3px 3px 3px rgba($color: #000000, $alpha: .4);
	transform-origin: top;
	transition: transform 400ms;
	transform: translate(-50%, -50%) perspective(1000px) rotateX(-90deg);
}

.edit-form.active {
	transform: translate(-50%, -50%) perspective(1000px) rotateX(0deg);
}

.edit-btn {
	display: flex;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	margin: .5em auto;
	width: 40px;
	height: 40px;
	line-height: 40px;
	text-align: center;
	border-radius: 50%;
	background: $main;
	color: #fff;
	font-size: 1.6em;
	transition: opacity 300ms;
	opacity: .7;
	z-index: 120;
	cursor: pointer;
	i {
		margin: auto;
		font-size: 20px;
	}
	&:hover {
		transition: opacity 400ms;
		opacity: 1;
	}
}


