nav {
	color: #fff;
	background-color: #ee6e73;
	width: 100%;
	height: 56px;
	line-height: 56px;
	z-index: 15;
	position: relative;
}

nav.nav-extended {
	height: auto;
}

nav.nav-extended .nav-wrapper {
	min-height: 56px;
	height: auto;
}

nav.nav-extended .nav-content {
	position: relative;
	line-height: normal;
}

nav a {
	color: #fff;
}

nav i,
nav [class^="mdi-"],
nav [class*="mdi-"],
nav i.material-icons {
	display: block;
	font-size: 24px;
	height: 56px;
	line-height: 56px;
}

nav .nav-wrapper {
	position: relative;
	height: 100%;
}

@media only screen and (min-width: 993px) {
	nav a.sidenav-trigger {
		display: none;
	}
}

nav .sidenav-trigger {
	float: left;
	position: relative;
	z-index: 1;
	height: 56px;
	margin: 0 18px;
}

nav .sidenav-trigger i {
	height: 56px;
	line-height: 56px;
}

nav .brand-logo {
	position: absolute;
	color: #fff;
	display: inline-block;
	font-size: 2.1rem;
	padding: 0;
}

nav .brand-logo.center {
	left: 50%;
	-webkit-transform: translateX(-50%);
	transform: translateX(-50%);
}

@media only screen and (max-width: 992px) {
	nav .brand-logo {
		left: 50%;
		-webkit-transform: translateX(-50%);
		transform: translateX(-50%);
	}
	nav .brand-logo.left,
	nav .brand-logo.right {
		padding: 0;
		-webkit-transform: none;
		transform: none;
	}
	nav .brand-logo.left {
		left: 0.5rem;
	}
	nav .brand-logo.right {
		right: 0.5rem;
		left: auto;
	}
}

nav .brand-logo.right {
	right: 0.5rem;
	padding: 0;
}

nav .brand-logo i,
nav .brand-logo [class^="mdi-"],
nav .brand-logo [class*="mdi-"],
nav .brand-logo i.material-icons {
	float: left;
	margin-right: 15px;
}

nav .nav-title {
	display: inline-block;
	font-size: 32px;
	padding: 28px 0;
}

nav ul {
	margin: 0;
}

nav ul li {
	-webkit-transition: background-color .3s;
	transition: background-color .3s;
	float: left;
	padding: 0;
}

nav ul li.active {
	background-color: rgba(0, 0, 0, 0.1);
}

nav ul a {
	-webkit-transition: background-color .3s;
	transition: background-color .3s;
	font-size: 1rem;
	color: #fff;
	display: block;
	padding: 0 15px;
	cursor: pointer;
}

